* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
  background-color: #212121;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.layers {
  perspective: 1000px;
  overflow: hidden;
}

.layers__container {
  height: 100vh;
  transform-style: preserve-3d;
  transform: rotateX(var(--move-y) ) rotateY(var(--move-x) );
}

.layers__item {
  background-position: center;
  background-size: cover;
  position: absolute;
  top: -15vh;
  bottom: -15vh;
  left: -15vh;
  right: -15vh;
}

.layer1 {
  transform: translateZ(-105px)scale(1.06);
}

.layer2 {
  width: 45px;
  height: 45px;
  top: 500px;
  left: 333px;
  transform: translateZ(65px)scale(.88);
}

.layer3 {
  top: 2%;
  left: 5%;
  transform: translateZ(115px)scale(.95);
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  gap: 10px;
  display: flex;
  top: 5%;
  left: 3%;
  transform: translateZ(105px)scale(.92);
}

.container-text {
  top: 12%;
  left: 2%;
  transform: translateZ(125px)scale(.88);
}

.text-about-us {
  font-size: 40px;
}

.text-for-answer {
  max-width: 700px;
}

.field {
  gap: 10px;
  font-size: 50px;
  display: flex;
}

.text {
  color: #fafafa;
  font-size: 30px;
}

.cube {
  perspective: 1200px;
  height: 360px;
  width: 360px;
  margin: auto;
  padding-top: 200px;
  position: relative;
}

.cube__container {
  transform-style: preserve-3d;
  height: inherit;
  width: inherit;
}

.cube__item {
  width: inherit;
  height: inherit;
  text-align: center;
  border: 3px solid #fff;
  position: absolute;
}

.cube__item span {
  cursor: pointer;
}

.front {
  background-image: url("cube1.c2aefba6.jpg");
  background-size: cover;
  transform: translateZ(180px);
}

.back {
  background-image: url("cube2.beee3007.jpg");
  background-size: cover;
  transform: rotateY(180deg)translateZ(180px);
}

.right {
  background-image: url("cube3.9469f10c.jpg");
  background-size: cover;
  transform: rotateY(90deg)translateZ(180px);
}

.left {
  background-image: url("cube4.979a27b6.jpg");
  background-size: cover;
  transform: rotateY(-90deg)translateZ(180px);
}

.top {
  background-image: url("cube5.e9a0bf73.jpg");
  background-size: cover;
  transform: rotateX(90deg)translateZ(180px);
}

.bottom {
  background-image: url("cube6.a9beea68.jpg");
  background-size: cover;
  transform: rotateX(-90deg)translateZ(180px);
}

main {
  background-image: url("https://i.ytimg.com/vi/KURW3vOo6uA/maxresdefault.jpg");
  background-size: cover;
  top: -10vh;
  bottom: -10vh;
  left: -10vh;
  right: -10vh;
}

.cube-n {
  width: 100%;
  height: max-content;
  justify-content: center;
  padding-top: 20px;
  display: flex;
}

.cube__container-n {
  max-width: 1200px;
  height: 100%;
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.cube__item-n {
  width: 350px;
  height: 350px;
  position: relative;
  transform: none;
}

.basicLightbox {
  width: 100%;
  height: 100vh;
  opacity: .01;
  z-index: 1000;
  will-change: opacity;
  background: rgba(0, 0, 0, .8);
  justify-content: center;
  align-items: center;
  transition: opacity .4s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.basicLightbox--visible {
  opacity: 1;
}

.basicLightbox__placeholder {
  max-width: 100%;
  z-index: 1;
  will-change: transform;
  transition: transform .4s;
  transform: scale(.9);
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  max-width: 95%;
  max-height: 95%;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  pointer-events: auto;
}

.basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  width: auto;
  height: auto;
}

.basicLightbox--iframe .basicLightbox__placeholder, .basicLightbox--img .basicLightbox__placeholder, .basicLightbox--video .basicLightbox__placeholder {
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.basicLightbox--visible .basicLightbox__placeholder {
  transform: scale(1);
}

